.casas .card-casa {
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 95%;
}
.casas .card-casa:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.casas .card-casa .card-casa--texto {
  background-color: white;
  color: #283339;
}
.casas .card-casa .card-casa--texto h3 {
  color: #283339;
  margin: 0 0 1rem 0;
}

.llamada-acion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  align-content: center;
  padding-bottom: 1rem;
}
.llamada-acion a {
  color: #F6F6F6;
}
.llamada-acion a:hover {
  color: #283339;
}

.footer {
  background-color: #EAB75C;
  padding-bottom: 1rem;
}
.footer h2 {
  color: #283339;
  margin: 0;
}
@media (max-width: 430px) {
  .footer h2 {
    text-align: center;
    padding-top: 2rem;
  }
}
.footer a {
  color: #283339;
  text-decoration: none;
}
.footer a:hover {
  color: #A68495;
}
.footer p {
  color: white;
}
.footer .footer--grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "contacto direccion content1 redes";
  color: white;
  gap: 1rem;
}
@media (max-width: 430px) {
  .footer .footer--grid {
    grid-template-columns: 1fr;
    grid-template-rows: 0.8fr 0.7fr 1fr 1fr;
    grid-template-areas: "contacto" "direccion" "content1" "redes";
  }
}
@media (min-width: 768px) {
  .footer .footer--grid {
    grid-template-columns: 236px 240px 100px 144px;
  }
}
@media (min-width: 992px) {
  .footer .footer--grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer .contacto {
  padding-left: 1rem;
  grid-area: contacto;
}
@media (max-width: 430px) {
  .footer .contacto {
    padding-left: 0rem;
    text-align: center;
  }
}
.footer .contacto p {
  margin: 0;
}
.footer .direccion {
  grid-area: direccion;
}
@media (min-width: 768px) {
  .footer .direccion {
    width: 250px;
    margin-left: 15px;
  }
}
.footer .direccion p {
  margin: 0;
}
@media (max-width: 430px) {
  .footer .direccion {
    text-align: center;
  }
}
.footer .menu {
  grid-area: content1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .footer .menu {
    margin-left: 7.625rem;
  }
}
@media (min-width: 992px) {
  .footer .menu {
    margin-left: 10rem;
  }
}
@media (max-width: 430px) {
  .footer .menu {
    margin-left: 0;
    text-align: center;
  }
}
.footer .redes {
  grid-area: redes;
  display: flex;
  flex-direction: column;
}
@media (max-width: 430px) {
  .footer .redes {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-left: 7rem;
  }
}
@media (min-width: 768px) {
  .footer .redes {
    margin-left: -3rem;
    justify-content: space-around;
  }
}
.footer .redes a img {
  width: 10%;
  margin-left: 25rem;
  margin-bottom: 20px;
}
@media (max-width: 430px) {
  .footer .redes a img {
    margin: 0;
    width: 30%;
  }
}
@media (min-width: 768px) {
  .footer .redes a img {
    width: 15%;
    margin-left: 12rem;
  }
}
@media (min-width: 992px) {
  .footer .redes a img {
    margin-left: 25rem;
  }
}

.contacto--enlaces {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  gap: 1rem;
}

.pie {
  background-color: #EAB75C;
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "a b c ";
}
@media (max-width: 430px) {
  .pie {
    grid-template-columns: none;
    grid-template-rows: 10px 1fr 1fr;
    grid-template-areas: "a" "b" "c";
  }
}
@media (max-width: 430px) {
  .pie {
    grid-template-columns: 1;
  }
}
.pie .logotipo-foot {
  grid-area: b;
  margin-left: 12rem;
}
.pie .logotipo-foot img {
  width: 39% !important;
}
@media (max-width: 430px) {
  .pie .logotipo-foot img {
    margin-left: 0rem;
  }
}
@media (min-width: 768px) {
  .pie .logotipo-foot img {
    width: 60% !important;
    margin-left: -3rem;
  }
}
@media (min-width: 992px) {
  .pie .logotipo-foot img {
    margin-left: 4rem;
    width: 40% !important;
  }
}
.pie .redes--foot {
  grid-area: c;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: space-evenly;
  align-items: end;
}
@media (max-width: 430px) {
  .pie .redes--foot {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-self: center;
  }
}
.pie .redes--foot img {
  width: 30% !important;
}
@media (max-width: 430px) {
  .pie .redes--foot img {
    width: 50% !important;
  }
}
.pie .redes--foot a {
  margin-left: 5rem;
}

.formulario {
  background-image: url(/build/img/Fotografia-Formulario.webp);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
@media (max-width: 430px) {
  .formulario {
    background-position: 72% 50%;
  }
}
.formulario legend {
  font-size: 2rem;
  color: #EAB75C;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: white;
  border: 1px solid #383838;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 992px) {
  .formulario input:not([type=submit]),
.formulario textarea, .formulario select {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .formulario input:not([type=submit]),
.formulario textarea, .formulario select {
    width: 50%;
  }
}
@media (max-width: 430px) {
  .formulario input:not([type=submit]),
.formulario textarea, .formulario select {
    width: 100%;
  }
}
@media (max-width: 280px) {
  .formulario input:not([type=submit]),
.formulario textarea, .formulario select {
    width: 100%;
  }
}
.formulario .btn-formulario {
  margin: 2rem 0 0 2rem;
}

.formulario-titulos {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.formulario-titulos h1, .formulario-titulos h2, .formulario-titulos p {
  color: #F6F6F6;
  text-align: center;
  margin: 1rem;
  background-color: hsla(38deg, 77%, 64%, 0.8);
  border-radius: 0.8rem;
  width: 40%;
}
@media (max-width: 430px) {
  .formulario-titulos h1, .formulario-titulos h2, .formulario-titulos p {
    width: 100%;
  }
}

.formu {
  margin: 0 auto;
  width: 600px;
  background-color: hsla(38deg, 77%, 64%, 0.8);
  border-radius: 0.8rem;
}
@media (max-width: 430px) {
  .formu {
    width: 374px;
  }
}
.formu fieldset {
  display: flex;
  flex-direction: column;
  border-radius: 0.8rem;
}
.formu fieldset input:not([type=submit]),
.formu fieldset textarea, .formu fieldset select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: white;
  border: 1px solid #383838;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

form {
  display: grid;
  gap: 1rem;
  grid-template-areas: "formu-info1 formu-info2";
}
@media (max-width: 430px) {
  form {
    grid-template-columns: 1;
    grid-template-rows: 256px 1fr;
    grid-template-areas: "formu-info1" "formu-info2";
  }
}
form .formu-info1 {
  grid-area: formu-info1;
}
form .formu-info2 {
  grid-area: formu-info2;
}

.requisitos {
  margin-top: 2rem;
}
.requisitos h2, .requisitos p {
  color: #F6F6F6;
}

.contenedor-escribiendo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.typing {
  width: 22ch;
  animation: typing 2s steps(22), blink 0.5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 2px solid;
}

@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}